import React from 'react'
import { Card, Table, Avatar } from 'antd';
import AvatarStatus from 'components/shared-components/AvatarStatus';
import Flex from 'components/shared-components/Flex'
import { Typography } from "antd";
import NumberFormat from 'react-number-format';
import utils from 'utils';

const { Title } = Typography;

const PaymentList = ({systemData}) => {
	const { inspected, avatarColors } = systemData;
	const tableColumns = [
		{
			title: 'Water Meter',
			dataIndex: 'meter',
			render: (_,record) => (
				<div className="d-flex" style={{cursor:'pointer'}}>
					<AvatarStatus size={30} src={<Avatar size={30} className="font-size-sm" style={{backgroundColor: avatarColors[Math.floor(Math.random() * avatarColors.length) - 1]}}>
						{utils.getNameInitial(record.client)}
						{/* <BgColorsOutlined style={{ fontSize: '16px'}}/> */}
					</Avatar>} name={record.watermeter} subTitle={record.client}/>
		 		</div>
		 	),
		},		
		{
			title: () => <div className="text-left">M<sup>3</sup></div>,
			dataIndex: 'cubicmeters',
			render: (_,record) => record.cubicmeters
		},
		{
			title: 'Amount',
			dataIndex: 'amount',
			render: (_,record) => (<NumberFormat displayType={'text'} value={parseInt(record.amount)} thousandSeparator={true}/>)
		},
		{
			title: 'Inspector',
			dataIndex: 'inspector',
			render: (_,record) => (
				<div className="d-flex align-items-center">
				  <Avatar size={30} className="font-size-sm" style={{backgroundColor: avatarColors[Math.floor(Math.random() * avatarColors.length) - 1]}}>
					{utils.getNameInitial(record.inspector)}
				  </Avatar>
				  <span className="ml-2">{record.inspector}</span>
				</div>
			  )
		},
		{
			title: 'Date',
			dataIndex: 'date',
			render: (_,record) => new Date(+record.datetime).toLocaleString()
		},
	];
	return (
		<Card>
			<Flex alignItems="center" justifyContent="between" mobileFlex={false}>
				<Flex className="mb-1" mobileFlex={false}>
					<div className="mr-md-3 mb-3">
						<Title level={4}>Recent Inspections</Title>
					</div>
				</Flex>
			</Flex>
			<div className="table-responsive">
				<Table 
					columns={tableColumns} 
					dataSource={ inspected } 
					rowKey='_id' 
				/>
			</div>
		</Card>
	)
}
export default PaymentList